.loader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.9);
}

.fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
}

.loader-animation {
    display: flex;
    gap: 10px;
}

.circle {
    width: 15px;
    height: 15px;
    background-color: #303538;
    border-radius: 50%;
    animation: bounce 0.6s infinite alternate;
}

.circle:nth-child(1) {
    animation-delay: 0s;
}
.circle:nth-child(2) {
    animation-delay: 0.2s;
}
.circle:nth-child(3) {
    animation-delay: 0.4s;
}
.circle:nth-child(4) {
    animation-delay: 0.6s;
}

@keyframes bounce {
    from {
        transform: translateY(0);
    }
    to {
        transform: translateY(-20px);
    }
}